"use client"

import { useState, useEffect } from "react"
import { Plus, Trash2, Archive, Mail } from "lucide-react"

interface InboxItem {
  id: string
  title: string
  message: string
  timestamp: string
  read: boolean
  archived: boolean
  type: "notification" | "reminder" | "message"
}

export default function InboxPage() {
  const [items, setItems] = useState<InboxItem[]>([])
  const [newTitle, setNewTitle] = useState("")
  const [newMessage, setNewMessage] = useState("")
  const [itemType, setItemType] = useState<"notification" | "reminder" | "message">("notification")
  const [filter, setFilter] = useState<"all" | "unread" | "archived">("all")

  useEffect(() => {
    const stored = localStorage.getItem("inbox")
    if (stored) {
      setItems(JSON.parse(stored))
    }
  }, [])

  useEffect(() => {
    localStorage.setItem("inbox", JSON.stringify(items))
  }, [items])

  const handleAddItem = () => {
    if (!newTitle.trim() || !newMessage.trim()) return
    const item: InboxItem = {
      id: Date.now().toString(),
      title: newTitle,
      message: newMessage,
      timestamp: new Date().toISOString(),
      read: false,
      archived: false,
      type: itemType,
    }
    setItems([item, ...items])
    setNewTitle("")
    setNewMessage("")
  }

  const handleMarkAsRead = (id: string) => {
    setItems(items.map((item) => (item.id === id ? { ...item, read: true } : item)))
  }

  const handleArchive = (id: string) => {
    setItems(items.map((item) => (item.id === id ? { ...item, archived: !item.archived } : item)))
  }

  const handleDelete = (id: string) => {
    setItems(items.filter((item) => item.id !== id))
  }

  const filteredItems = items.filter((item) => {
    if (filter === "unread") return !item.read && !item.archived
    if (filter === "archived") return item.archived
    return !item.archived
  })

  const getTypeColor = (type: string) => {
    switch (type) {
      case "notification":
        return "bg-blue-100/50 text-blue-700 border-blue-200"
      case "reminder":
        return "bg-purple-100/50 text-purple-700 border-purple-200"
      case "message":
        return "bg-green-100/50 text-green-700 border-green-200"
      default:
        return ""
    }
  }

  const stats = {
    total: items.length,
    unread: items.filter((i) => !i.read && !i.archived).length,
    archived: items.filter((i) => i.archived).length,
  }

  return (
    <div className="p-4 lg:p-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-foreground mb-2">Inbox</h1>
        <p className="text-muted-foreground">Manage your notifications and reminders</p>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-3 gap-4 mb-8">
        <div className="bg-card rounded-lg p-4 border border-border">
          <p className="text-sm text-muted-foreground mb-1">Total</p>
          <p className="text-2xl font-bold text-foreground">{stats.total}</p>
        </div>
        <div className="bg-card rounded-lg p-4 border border-border">
          <p className="text-sm text-muted-foreground mb-1">Unread</p>
          <p className="text-2xl font-bold text-secondary">{stats.unread}</p>
        </div>
        <div className="bg-card rounded-lg p-4 border border-border">
          <p className="text-sm text-muted-foreground mb-1">Archived</p>
          <p className="text-2xl font-bold text-accent-foreground">{stats.archived}</p>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* Add Item Form */}
        <div className="bg-card rounded-xl shadow-md p-6 border border-border lg:col-span-1 h-fit">
          <h2 className="text-lg font-bold text-foreground mb-4">New Item</h2>
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Title</label>
              <input
                type="text"
                value={newTitle}
                onChange={(e) => setNewTitle(e.target.value)}
                placeholder="Item title"
                className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Message</label>
              <textarea
                value={newMessage}
                onChange={(e) => setNewMessage(e.target.value)}
                placeholder="Message details"
                rows={4}
                className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Type</label>
              <select
                value={itemType}
                onChange={(e) => setItemType(e.target.value as any)}
                className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
              >
                <option value="notification">Notification</option>
                <option value="reminder">Reminder</option>
                <option value="message">Message</option>
              </select>
            </div>
            <button
              onClick={handleAddItem}
              className="w-full bg-primary hover:bg-primary/90 text-primary-foreground px-4 py-2 rounded-lg font-medium transition-colors flex items-center justify-center gap-2"
            >
              <Plus size={20} />
              Add Item
            </button>
          </div>
        </div>

        {/* Items List */}
        <div className="lg:col-span-2 space-y-4">
          {/* Filter Buttons */}
          <div className="flex gap-2 mb-4 flex-wrap">
            {["all", "unread", "archived"].map((f) => (
              <button
                key={f}
                onClick={() => setFilter(f as any)}
                className={`px-4 py-2 rounded-lg font-medium transition-colors ${
                  filter === f
                    ? "bg-primary text-primary-foreground"
                    : "bg-muted text-muted-foreground hover:bg-muted/80"
                }`}
              >
                {f.charAt(0).toUpperCase() + f.slice(1)}
              </button>
            ))}
          </div>

          {/* Items */}
          <div className="space-y-3">
            {filteredItems.length === 0 ? (
              <div className="text-center py-12 bg-card rounded-xl border border-border">
                <Mail className="w-12 h-12 text-muted-foreground mx-auto mb-3 opacity-50" />
                <p className="text-muted-foreground">No items in this view</p>
              </div>
            ) : (
              filteredItems.map((item) => (
                <div
                  key={item.id}
                  className={`p-4 rounded-lg border transition-all cursor-pointer ${
                    item.read
                      ? "bg-muted/30 border-border/50"
                      : "bg-card border-secondary hover:border-secondary/80 shadow-md"
                  }`}
                  onClick={() => handleMarkAsRead(item.id)}
                >
                  <div className="flex items-start gap-4">
                    <div className="flex-1">
                      <div className="flex items-center gap-2 mb-2">
                        <h3 className={`font-bold ${item.read ? "text-muted-foreground" : "text-foreground"}`}>
                          {item.title}
                        </h3>
                        <span className={`text-xs font-medium px-2 py-1 rounded border ${getTypeColor(item.type)}`}>
                          {item.type}
                        </span>
                        {!item.read && <div className="w-2 h-2 bg-secondary rounded-full ml-auto"></div>}
                      </div>
                      <p className="text-sm text-muted-foreground mb-2">{item.message}</p>
                      <p className="text-xs text-muted-foreground/60">{new Date(item.timestamp).toLocaleString()}</p>
                    </div>
                    <div className="flex gap-2">
                      <button
                        onClick={(e) => {
                          e.stopPropagation()
                          handleArchive(item.id)
                        }}
                        className="p-2 hover:bg-muted rounded transition-colors"
                        title={item.archived ? "Unarchive" : "Archive"}
                      >
                        <Archive size={18} className="text-muted-foreground hover:text-foreground" />
                      </button>
                      <button
                        onClick={(e) => {
                          e.stopPropagation()
                          handleDelete(item.id)
                        }}
                        className="p-2 hover:bg-destructive/10 rounded transition-colors"
                      >
                        <Trash2 size={18} className="text-destructive" />
                      </button>
                    </div>
                  </div>
                </div>
              ))
            )}
          </div>
        </div>
      </div>
    </div>
  )
}
